<template>
  <div class="m-main">
    <div class="m-my-books" v-if="myBooks.length > 0">
      <div class="m-my-books-content">
        <div 
          v-for="(book, index) in myBooks" 
          :key="book.id" 
          class="m-my-books-item"
          :class="[ index === operation.currentIndex && (operation.isShowOperation ? 'show-operation' : 'hide-operation') ]" 
          @touchstart="handleTouchStart"
          @touchmove="handleTouchMove"
          @touchend="handleTouchEnd($event, index)" >
          <div class="m-my-books-checkbox-wrap" >
            <input type="checkbox" :checked="book.checked" @click="handleChecked(book.id, $event)" class="m-my-books-checkbox" >
          </div>
          <div class="m-my-books-img-wrap">
            <img :src="book.avatar" class="m-my-books-img">
          </div>
          <div class="m-my-books-info" >
            <div>{{book.title}}</div>
            <div>￥{{book.price}}</div>
            <div class="m-add-info">
              <button class="m-btn" @click="handleSub(book.id)">减</button>
              <input type="text" :value="book.count" placeholder="数量" class="m-input" @input="handleInput(book.id, $event)">
              <button class="m-btn" @click="handleAdd(book.id)" >加</button>
            </div>        
          </div>
          <div class="m-my-books-delete" @click="handleDelete(book.id)">
            删除
          </div>
        </div>
      </div>
      <div class="m-my-books-footer">
        <label>
          <input type="checkbox" :checked="total.checkedAll" @click="handleCheckedAll">
          全选
        </label>
        <button @click="handleDeleteChecked">删除</button>
        <span>
          合计: ￥{{total.totalPrice}}
        </span>
      </div>
    </div>
    <div v-else class="m-my-books-empty">
      购物车是空空如也~
    </div>
  </div>
</template>

<script>
import Api from '../api'

let touchStart, touchEnd

export default {
  data() {
    return {
      operation: {}
    }
  },
  computed: {
    myBooks() {
      return this.$store.state.myBooks
    },
    total() {
      let myBooks = this.$store.state.myBooks
      let totalPrice = myBooks.filter(item => item.checked).reduce((totalPrice, item) => {
        return totalPrice + item.count * item.price
      }, 0)
      return {
        checkedAll: myBooks.every(item => item.checked),
        totalPrice
      }
    }
  },
  methods: {
    handleAdd(id) {
      this.$store.dispatch({ type: 'myBooks', data: { id, operation: 'add' }, method: 'patch' })
    },
    handleSub(id) {  
      this.$store.dispatch({ type: 'myBooks', data: { id, operation: 'sub' }, method: 'patch' })   
    },
    handleInput(id, e) {
      let myBooks = this.myBooks
      let count = e.target.value.replace(/[^\d]/g, '') - 0
      if (count === 0) {
        count = 1
      }
      this.$store.dispatch({ type: 'myBooks', data: { id, operation: 'inputCount', count }, method: 'patch' })
    },
    handleChecked(id, e) {
      this.$store.dispatch({ type: 'myBooks', data: { id, operation: 'checked', checked: e.target.checked }, method: 'patch' })
    },
    handleCheckedAll(e) {
      this.$store.dispatch({ type: 'myBooks', data: { operation: 'checkedAll', checked: e.target.checked }, method: 'patch' })
    },     
    handleTouchStart(e) {
      console.log('start', e)
      console.log(e.changedTouches[0].pageX)
      touchStart = e.changedTouches[0].pageX
    },
    handleTouchMove(e) {
      //console.log('move', e)
      //console.log(e.changedTouches[0].pageX)
    },
    handleTouchEnd(e, index) {
      console.log('end', e)
      console.log(e.changedTouches[0].pageX)
      touchEnd = e.changedTouches[0].pageX
      if (touchStart - touchEnd > 0) {
        this.operation = { currentIndex: index, isShowOperation: true }
      } else if (touchStart - touchEnd < 0 ){
        this.operation = { currentIndex: index, isShowOperation: false }
      }
    },
    handleDelete(id) {
      this.$store.dispatch({ type: 'myBooks', data: { ids: [id] }, method: 'delete', callback: () => {
        this.operation = {}
      }})
    },
    handleDeleteChecked() {
      let ids = this.myBooks.filter(item => item.checked).map(item => item.id)
      this.$store.dispatch({ type: 'myBooks', data: { ids }, method: 'delete', callback: () => {
        this.operation = {}
      }})
    }
  }
}
</script>

